import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { List, Button, message } from 'antd';

const CategoriesModal = ({ shoppingListId, onAdded }) => {
    const [categories, setCategories] = useState([]);
    const [selectedCategory, setSelectedCategory] = useState(null);
    const [products, setProducts] = useState([]);
    const [isProductsVisible, setIsProductsVisible] = useState(false);

    useEffect(() => {
        fetchCategories();
    }, []);

    const fetchCategories = async () => {
        try {
            const { data } = await axios.get('http://192.168.92.34:3000/api/categories');
            setCategories(data);
        } catch (error) {
            console.error('   ');
        }
    };

    const selectCategory = async (categoryId) => {
        try {
            const { data } = await axios.get(`http://192.168.92.34:3000/api/categories/${categoryId}`);
            setProducts(data);
            setSelectedCategory(categoryId);
            setIsProductsVisible(true);
        } catch (error) {
            console.error('    ');
        }
    };

    const addProductToList = async (productId) => {
        try {
            await axios.post(`http://192.168.92.34:3000/api/shoppingLists/${shoppingListId}/products/${productId}`);
            message.success('   ');
            onAdded();
            setIsProductsVisible(false);
        } catch (error) {
            message.error('     ');
        }
    };

    return (
        <>
            {!isProductsVisible ? (
                <List
                    dataSource={categories}
                    renderItem={category => (
                        <List.Item key={category.id} actions={[<Button onClick={() => selectCategory(category.id)}></Button>]}>
                            {category.name}
                        </List.Item>
                    )}
                />
            ) : (
                <>
                    <Button onClick={() => setIsProductsVisible(false)} style={{ marginBottom: 16 }}>
                          
                    </Button>
                    <List
                        dataSource={products}
                        renderItem={product => (
                            <List.Item key={product.id} actions={[<Button onClick={() => addProductToList(product.id)}>  </Button>]}>
                                {product.name}
                            </List.Item>
                        )}
                    />
                </>
            )}
        </>
    );
};

export default CategoriesModal;
